import axios from 'axios';
import React, { useEffect, useRef, useState } from 'react'
import { feFlood } from 'react-dom-factories';
import { Redirect, Route, Switch, useHistory, useParams } from 'react-router'
import { BrowserRouter, Link } from 'react-router-dom';
import Navbar from '../template/navbar'

const getToken = () => {
  const tokenString = localStorage.getItem('token');
  const userToken = JSON.parse(tokenString);
  return userToken?.token
}

const FormD = ({ role }) => {

  const [red, setRed] = useState(false);

  if (red) {
    return <Redirect to="/home" />
  }

  return (
    <BrowserRouter basename="/link-online">
      <div className="container h-100v">
        <div className="row h-100">
          <Navbar user={role?.role[0]} setRed={setRed} />
          <div className="col d-flex h-100v p-5" style={{ overflow: 'auto', paddingTop: 130 }}>
            <Switch>
              <Route exact path="/">
                <FormBegin />
              </Route>
              <Route exact path="/:params">
                <FormProcess />
              </Route>
              <Route exact path="/:params/:form">
                <FormDone role={role} />
              </Route>
            </Switch>
          </div>
        </div>
      </div>
    </BrowserRouter>
  )
}

const FormBegin = () => {
  return (
    <div className="wrap-b d-flex flex-direction-column gap-5 h-100 px-10 py-7">
      <Link className="wrap-b deposit-div h-100 d-flex p-5 justify-content-space-between" to="/nabung">
        <div className="d-flex align-items-center gap-4">
          <img src={process.env.PUBLIC_URL + '/image/money.png'} height="90%" alt=""/>
          <div className="d-flex flex-direction-column">
            <h1 className="h1-log color-green100">Savings</h1>
            <p className="color-green300 text-dep">Save your money here !</p>
          </div>
        </div>
        <div className="d-flex align-items-end">
          <button className="borderRadius-7 background-green100 p-7-15 color-white">Click Here</button>
        </div>
      </Link>
      <Link className="wrap-b loan-div h-100 d-flex p-5 justify-content-space-between" to="/pinjam">
        <div className="d-flex align-items-center gap-4">
          <img src={process.env.PUBLIC_URL + '/image/pay.png'} height="90%" alt=""/>
          <div className="d-flex flex-direction-column">
            <h1 className="h1-log color-blues100">Loans</h1>
            <p className="color-blues300 text-dep">Borrow money from here !</p>
          </div>
        </div>
        <div className="d-flex align-items-end">
          <button className="borderRadius-7 background-blues100 p-7-15 color-white">Click Here</button>
        </div>
      </Link>
    </div>
  )
}

const FormProcess = () => {

  const { params } = useParams();

  return (
    <div className="wrap-b d-flex gap-5 h-100 px-10 py-7">
      <Link className="wrap-b deposit-div flex-direction-column w-100 d-flex p-5 justify-content-space-between borderRadius-50" to={`${params}/0`}>
        <div className="img-profile background-green100 position-relative align-self-end" style={{ width: '30%', height: 0, paddingTop: '30%' }}>
          <div className="position-absolute d-flex align-items-center justify-content-center" style={{ top: 0, bottom: 0, left: 0, right: 0 }}>
            <img src={process.env.PUBLIC_URL + '/image/arrow.png'} style={{ width: '50%', transform: 'rotate(180deg)', }} alt=""/>
          </div>
        </div>
        <div className="d-flex flex-direction-column">
          <h1 className="h1-log color-green100">{params === 'nabung' ? 'Deposit' : 'Pay' }</h1>
          <h3 className="color-green300 text-dep">{params === 'nabung' ? 'Tambah Uang' : 'Bayar Hutang' }</h3>
        </div>
        <div className="d-flex align-items-end">
          <button className="borderRadius-7 background-green100 p-7-15 color-white">Click Here</button>
        </div>
      </Link>
      <Link className="wrap-b loan-div flex-direction-column w-100 d-flex p-5 justify-content-space-between borderRadius-50" to={`${params}/1`}>
        <div className="d-flex align-items-end">
          <button className="borderRadius-7 background-blues100 p-7-15 color-white">Click Here</button>
        </div>
        <div className="d-flex flex-direction-column">
          <h1 className="h1-log color-blues100">{params === 'nabung' ? 'Withdraw' : 'Request' }</h1>
          <h3 className="color-blues300 text-dep">{params === 'nabung' ? 'Ambil Uang' : 'Tambah Hutang' }</h3>
        </div>
        <div className="img-profile background-blues100 position-relative align-self-end" style={{ width: '30%', height: 0, paddingTop: '30%' }}>
          <div className="position-absolute d-flex align-items-center justify-content-center" style={{ top: 0, bottom: 0, left: 0, right: 0 }}>
            <img src={process.env.PUBLIC_URL + '/image/arrow.png'} style={{ width: '50%' }} alt=""/>
          </div>
        </div>
      </Link>
    </div>
  );
}

const FormDone = ({ role }) => {

  const { params, form } = useParams();
  const history = useHistory();

  const [data, setDatas] = useState('');
  const [id, setId] = useState(0);

  const ff = useRef();

  const onSubmit = (e) => {
    e.preventDefault();

    let datas = {};
    let y = e.target.querySelectorAll('input');

    for (let index = 0; index < y.length; index++) {
      datas[y[index].name] = y[index].value
    }

    let method = 'POST';

    if (form === '1' && params === 'nabung') {
      method = 'PUT'
    }

    delete datas['test'];
    delete datas['name'];

    datas['data_id'] = role?.data?.[0]?.id
    datas['user_id'] = role?.id
    datas['waiting_confirmation'] = null;

    axios({
      method: method,
      url: `https://coperationv2.herokuapp.com/api/${params}${params === 'pinjam' ? form === '0' ? '-pay' : '-req' : form === '0' ? `-req` : `-edit/${id}`}`,
      data: datas,
      headers: {
        ContentType: 'multipart/form-data',
        Authorization: `Bearer ${getToken()}`
      }
    }).then(() => history.push('/')).catch(error => console.log(JSON.stringify(error)))
  }

  useEffect(() => {
    axios.get(
      `https://coperationv2.herokuapp.com/api/data/${role?.name}`,
      {
        headers: {
          Authorization: `Bearer ${getToken()}`,
        }
      }
    ).then(data => {setDatas(data?.data?.[0]?.[params === 'nabung' ? 'deposit' : 'loans']?.[data?.data?.[0]?.[params === 'nabung' ? 'deposit' : 'loans']?.length - 1]?.total); setId(data?.data?.[0]?.[params === 'nabung' ? 'deposit' : 'loans']?.[data?.data?.[0]?.[params === 'nabung' ? 'deposit' : 'loans']?.length - 1]?.id)}).catch(() => {});
  }, [params, form]);

  useEffect(() => {
    ff.current.querySelector('input[name=test]').value = data;
  }, [data]);

  return (
    <form className="d-flex h-100 justify-content-center align-items-center w-100" ref={ff} onSubmit={onSubmit}>
      <div className="wrap-b col-7 h-75 flex-direction-column d-flex">
        <div className={"w-100 text-center py-2 color-white background-" + (form === '0' ? 'green300' : 'red300' )}>
          <h3>{params.trim().replace(/^\w/, (c) => c.toUpperCase())}</h3>
        </div>
        <div className="h-100 d-flex flex-direction-column align-items-center justify-content-center">
          <div className="col-6 col-md-8 d-flex gap-4 flex-direction-column">
            <div className="d-flex justify-content-space-between gap-4 align-items-center justify-content-start">
              <h3 className="col-3">Name</h3>
              <input type="text" name='name' disabled defaultValue={role?.name} className="input-modal border-grey300 w-100" />
            </div>
            <div className="d-flex justify-content-space-between gap-4 align-items-center justify-content-start">
              <h3 className="col-3">{params.trim().replace(/^\w/, (c) => c.toUpperCase())}</h3>
              <input type="text" name='test' disabled defaultValue="0" className="input-modal border-grey300 w-100" />
            </div>
            <div className="d-flex justify-content-space-between gap-4 align-items-center">
              <h3 className="col-3">{params === 'pinjam' ? form === '0' ? 'Nominal' : 'Total' : 'Total' }</h3>
              <input type="number" min="0" name={params === 'pinjam' ? form === '0' ? 'nominal' : 'total' : 'total' } className="input-modal border-grey300 w-100" />
            </div>
            {(form === '1' && params ==='pinjam') && <div className="d-flex justify-content-space-between gap-4 align-items-center">
              <h3 className="col-3">Return</h3>
              <input type="date" name="return" className="input-modal border-grey300 w-100" />
            </div>}
          </div>
        </div>
        <div className="d-flex justify-content-end p-3 gap-3">
          <button className="py-1 px-3 borderRadius-2 background-gray300" type="button" onClick={() => history.goBack()}><h3>Back</h3></button>
          <button className={"py-1 px-3 color-white borderRadius-2 background-" + (form === '0' ? 'green300' : 'red300')} type="submit"><h3>Submit</h3></button>
        </div>
      </div>
    </form>
  )
}

export default FormD
