import axios from 'axios';
import moment from 'moment';
import React, { useEffect, useRef, useState } from 'react'

const ModalUser = ({ datas, show, setShow }) => {

  const modal = useRef(null);

  const [items, setItems] = useState('');
  const [data, setData] = useState([]);

  const getToken = () => {
    const tokenString = localStorage.getItem('token');
    const userToken = JSON.parse(tokenString);
    return userToken?.token
  }

  const modalClose = () => {
    return false;
  }

  const tabs = (e) => {
    let y = modal.current.querySelector('#userSide').children;
    for (let index = 0; index < y.length; index++) {
      y[index].classList.remove('active');
    }
    e.target.classList.add('active')
    setItems(e.target.innerHTML);
  }

  const escp = (e) => {
    if (e.keyCode === 27) {
      setShow(false);
      return document.removeEventListener('keydown', escp)
    }
  }

  useEffect(() => {
    let y = modal.current.querySelector('#userSide').children;
    for (let index = 0; index < y.length; index++) {
      y[index].addEventListener('click', tabs)
    }
    y[0].click();
  }, [modal]);

  useEffect(() => {
    if (show) {
      modal.current.querySelector('#userSide').children[0].click();
      document.addEventListener('keydown', escp)
      modal.current.classList.add('d-flex')
      const get = async (url) => {
        axios.get(
          `https://coperationv2.herokuapp.com/api/data/${url}`,
          {
            headers: {
              'Content-Type': 'application/json',
              'Authorization': 'Bearer ' + getToken()
            }
          }
        ).then((data) => setData(data.data))
      }
      get(datas?.data?.[0]?.name)
    } else {
      modal.current.classList.remove('d-flex')
    }
  }, [show]);

  const UserItems = () => {
    let t = [];
    if (items === 'Details') {
      t.push(
        <div className="d-flex flex-direction-column gap-5 h-100" key={"user0"}>
          <div className="d-flex gap-3">
            <div className="img-profile position-relative" style={{ width: '15%', height: 0, paddingTop: '15%' }}>
              <img src={`https://coperationv2.herokuapp.com/img_profile/${datas?.image}`} alt="" style={{ top: 0 }} className="position-absolute" />
            </div>
            <div className="d-flex flex-direction-column justify-content-space-around">
              <p>Name: {datas?.name}</p>
              <p>Email: {datas?.email}</p>
              <p>Role: {datas?.role}</p>
            </div>
          </div>
          <div className="background-gray600 borderRadius-2 p-3 h-100 d-flex flex-direction-column">
            <p>Data User</p>
            <p>Alamat: {datas?.data?.[0]?.alamat}</p>
            <p>No HP: {datas?.data?.[0]?.no_telp}</p>
          </div>
        </div>
      );
    } else if (items === 'Products') {
      t.push(
        <div className="d-grid grid-template-column-2 gap-2" key={"user0"}>
          {data[0]?.barang?.length < 1
           ? <div>No Data !</div>
           : data[0]?.barang?.map((dataa, index) => (
            <div className="background-blues300 color-white borderRadius-2 p-3 position-relative" key={index}>{dataa?.name_barang}: {dataa?.stock} <span className="position-absolute" style={{ right: 20 }}>Rp. {dataa?.harga}</span></div>
          ))}
        </div>
      )
    } else if (items === 'Savings') {
      t.push(
        <div className="d-grid grid-template-column-2 gap-2" key={"user0"}>
          {data[0]?.deposit?.length < 1
           ? <div>No Data !</div>
           : data[0]?.deposit?.map((dataa, index) => (
            <div className="background-blues300 color-white borderRadius-2 p-3 position-relative" key={index}>{moment(dataa?.created_at).format('YYYY/MMMM/DD : ') + dataa?.total}</div>
          ))}
        </div>
      )
    } else if (items === 'Debts') {
      t.push(
        <div className="d-grid grid-template-column-2 gap-2" key={"user0"}>
          {data[0]?.loans?.length < 1
           ? <div>No Data !</div>
           : data[0]?.loans?.map((dataa, index) => (
            <div className="background-blues300 color-white borderRadius-2 p-3 position-relative" key={index}>{moment(dataa?.created_at).format('YYYY/MMMM/DD : ') + dataa?.total} <span className="position-absolute" style={{ right: 20 }}>{dataa?.waiting_confirmation}</span></div>
          ))}
        </div>
      )
    }
    return t;
  }

  return (
    <div id="modalUser" className="modal justify-content-center align-items-center p-5" ref={modal} onClick={modalClose}>

      <div className="modal-content col-12 h-100 wrap-b position-relative d-flex">
        <div className="user-side background-blues300 color-white d-flex flex-direction-column overflow-hidden" id="userSide">
          <h3>Details</h3>
          <h3>Products</h3>
          <h3>Savings</h3>
          <h3>Debts</h3>
          <h3 onClick={() => setShow(false)}>Back</h3>
        </div>
        <div className="user-det p-3 w-100">
          <UserItems />
        </div>
      </div>

    </div>
  )
}

export default ModalUser
