import React, { useEffect, useRef, useState } from 'react'
import UserData from '../data/UserData'
import ModalUser from '../Modal/modalUser'

const ListUser = ({}) => {

  const wrapT = useRef();

  const [show, setShow] = useState(false);
  const [datas, setDatas] = useState({});
  const [view, setView] = useState('');

  const roled = (e) => {
    let t = wrapT.current.getElementsByTagName('p');
    for (let index = 0; index < t.length; index++) {
      t[index].classList.remove('active');
    }
    e.target.classList.add('active')
    setView(e.target.innerHTML.toLowerCase());
  }

  useEffect(() => {
    wrapT.current.getElementsByTagName('p')[0].click();
  }, []);

  return (
    <div className="chart-page d-flex w-100 justify-content-center wrapi">
      <ModalUser show={show} setShow={setShow} datas={datas} />
      <div className="role-wrap col d-flex flex-direction-column">
        <div className="chart-text d-flex justify-content-space-between w-100 align-items-center">
          <div className="wrap-ty d-flex align-items-center gap-2" ref={wrapT}>
            <h3>Member Role:</h3>
            <p className="p-7-15 cursor-pointer" onClick={roled}>
              Admin
            </p>
            <p className="p-7-15 cursor-pointer" onClick={roled}>
              Anggota
            </p>
            <p className="p-7-15 cursor-pointer" onClick={roled}>
              Penjual
            </p>
          </div>
          <div className="chart-se">
            <div className="role-se d-flex align-items-center">
              <i className="material-icons">search</i>
              <input type="text" />
            </div>
          </div>
        </div>
        <div className="role-list d-grid">
          <UserData setDatas={setDatas} setShow={setShow} view={view} />
        </div>
      </div>
    </div>
  )
}

export default ListUser;
