import axios from 'axios';
import PropTypes from 'prop-types';
import React, { useEffect, useRef, useState } from 'react'
import UseAnimations from 'react-useanimations';
import menu4 from 'react-useanimations/lib/menu4';
import UseAuth from '../auth';
import Modal2 from '../Modal/Modal2';
import userPlus from 'react-useanimations/lib/userPlus'
import ListBar from './listBar';
import ModalC from '../Modal/modalC';
import { useHistory } from 'react-router';
import { EventSourcePolyfill } from 'event-source-polyfill';

const Logout = async token => {
  return axios.post(
    'https://coperationv2.herokuapp.com/api/logout', {},
    {
      headers: {
          'Content-Type': 'application/json',
          'Authorization': 'Bearer ' + token
      },
    })
   .then(data => {
     return data.data.message;
   })
   .catch(data => {
     return {'error': data}
   });
}

const Navbar = ({ setRed, user, sized }) => {

  const NItR = useRef();

  const { setToken } = UseAuth();
  const [use, setUse] = useState(false);
  const [onModal, setModal] = useState(false);
  const [size, setSize] = useState(80);
  const [sizeA, setSizeA] = useState(24);
  const [visible, setVisible] = useState(true);
  const [list, setList] = useState(false);
  const [menuB, setMenuB] = useState(false);
  const [height, setHeight] = useState('auto');
  const [pinjam, setPinjam] = useState([]);
  const [pay, setPay] = useState([]);

  const history = useHistory();
  
  const getToken = () => {
    const tokenString = localStorage.getItem('token');
    const userToken = JSON.parse(tokenString);
    return userToken?.token;
  }
  
  const evtSource = new EventSourcePolyfill(`https://coperationv2.herokuapp.com/api/pinjam-req/list`, {
    headers: {
      Authorization: `Bearer ${getToken()}`,
      ContentType: 'application/json',
      Accept: 'application/json',
      // CacheControl: 'no-cache',
      // Connection: 'keep-alive',
      // 'X-Accel-Buffering': 'no',
      'Access-Control-Allow-Origin': '*'
    },
  });
  
  useEffect(() => {
    getToken();
  }, []);

  const handleSubmit = async e => {
    e.preventDefault();
    const tokenA = await Logout(getToken());
    if (tokenA?.error === undefined) {
      setToken({'logout': true});
      setRed(true)
    }
  }

  const addUser = (e) => {
    e.preventDefault();
    setUse(true);
  }

  const resizeW = () => {
    if (window.innerWidth <= 768) {
      setSize(60);
      setSizeA(15);
      setVisible(false);
    } else {
      setSize(80);
      setSizeA(24);
      setVisible(true);
    }
  }

  const approve = ({ params, id, accepted }) => {
    axios.post(
      `https://coperationv2.herokuapp.com/api/${params}-accept/${id}`,
      {
        'waiting_confirmation': accepted
      },
      {
        headers: {
          Authorization: `Bearer ${getToken()}`
        }
      }
    ).then(() => get()).catch(error => console.log(error))
  }

  useEffect(() => {
    resizeW();
    window.addEventListener('resize', resizeW);
    return () => window.removeEventListener('resize', resizeW);
  }, [window.innerWidth]);

  const resizeH = () => {
    setHeight(`calc(${window.innerHeight}px - 60px)`)
  }

  useEffect(() => {
    resizeH();

    window.addEventListener('resize', resizeH);
    return () => window.removeEventListener('resize', resizeH);
  }, [window.innerHeight]);

  useEffect(() => {
    if (sized !== undefined) {
      setList(true)
    }
  }, []);

  const setB = () => {
    setMenuB(!menuB);
  }

  useEffect(() => {
    if (menuB) {
      NItR.current.classList.add('animate5')
    } else {
      NItR.current.classList.remove('animate5')
    }
  }, [menuB]);

  const myFunction = () => {
    var popup = document.getElementById('popNav');
    popup.classList.toggle("show");
  }

  const get = async () => {
    axios.get(
      `https://coperationv2.herokuapp.com/api/pinjam-req/list`,
      {
        headers: {
          'Authorization': `Bearer ${getToken()}`,
        }
      }
    ).then(data => setPinjam(data.data)).catch(() => {});
    axios.get(
      `https://coperationv2.herokuapp.com/api/pay-req/list`,
      {
        headers: {
          'Authorization': `Bearer ${getToken()}`,
        }
      }
    ).then(data => setPay(data.data)).catch(() => {});
  }

  useEffect(() => {
    if (user === 'admin') {
      get();
      // console.log(evtSource)
      evtSource.onmessage = (data) => console.log(data);
      evtSource.onerror = (error) => console.log(error);
      evtSource.onopen = (data) => console.log(data);
    }
  }, []);

  return (
    <>
    {user === 'admin' && <Modal2 use={use} setUse={setUse} />}
    <ModalC use={onModal} setModal={setModal} />
    <div className="d-flex position-absolute justify-content-space-between w-100 nav-responsive" style={{ zIndex: 2 }}>
      <div className={"d-flex align-items-center position-relative " + (list ? 'topnav-responsive' : '' )} style={{ height: `${size}px`, padding: '20px' }}>
        <UseAnimations animation={menu4} strokeColor="white" className="icons" reverse={menuB} onClick={setB} />
        <div className={"gap-2" + (list ? ' nav-item-responsive' : '')} style={{ overflow: 'hidden auto', maxHeight: height }} ref={NItR}>
          <div className="navbar-brander align-items-center" onClick={() => history.push('/')}>
            <img src={process.env.PUBLIC_URL + '/image/icon.png'} alt="" height={`${size}px`} />
            <h2 className="nav-brand color-md-white">Kaufen Market</h2>
          </div>
          {list ? <ListBar /> : null}
        </div>
      </div>
      <div className="top-nav" style={{ height: `${size}px`, right: 0, zIndex: 1 }}>
        <ul className="top-item">
          {user !== 'admin' && <li className="toa cursor-pointer" onClick={() => history.push('/link-online')}><i className="material-icons">monetization_on</i></li>}
          {user === 'admin' && <li className="toa cursor-pointer" onClick={addUser}>
            <UseAnimations animation={userPlus} size={sizeA} strokeColor="white" />
          </li>}
          <li className="toa cursor-pointer">
            <i className="material-icons" onClick={(e) => {e.preventDefault(); setModal(true)}}>lock_open</i>
          </li>
          <li className="toa cursor-pointer position-relative">
            <i className="material-icons" onClick={myFunction}>notifications</i>
            <span className="popuptext popnav cursor-normal d-flex flex-direction-column whiteSpace-noWrap p-3" id="popNav">
              {user === 'admin' ? (pay.length < 1 && pinjam.length < 1)
               ? 'No Notifications !' 
               : <div className="d-flex flex-direction-column gap-1">
                {pinjam.length < 1 ? null
                : <>
                <span>Pinjam List</span>
                {pinjam.map((data, index) => (
                  <span className="background-blues300 color-white borderRadius-2 overflow-hidden p-2 position-relative d-flex gap-1 flex-direction-column" key={'pinjam'+index}>{data?.name} <span>Rp. {data?.total}</span> <span className="d-flex gap-1 align-items-center" style={{ right: 10, top: 0, bottom: 0 }}><span className="material-icons cursor-pointer color-red300" onClick={() => approve({ id: data?.id, params: 'pinjam', accepted: 'rejected' })}>cancel</span><span className="material-icons cursor-pointer color-green300" onClick={() => approve({ id: data?.id, params: 'pinjam', accepted: 'accepted' })}>check_circle</span></span></span>
                ))}
                </>}
                {pay.length < 1 ? null
                : <>
                <span>Pay List</span>
                {pay.map((data, index) => (
                  <span className="background-blues300 color-white borderRadius-2 overflow-hidden p-2 position-relative d-flex gap-1 flex-direction-column" key={'pay'+index}>{data?.name} <span>Rp. {data?.nominal}</span> <span className="d-flex gap-1 align-items-center" style={{ right: 10, top: 0, bottom: 0 }}><span className="material-icons cursor-pointer color-red300" onClick={() => approve({ id: data?.id, params: 'pay', accepted: 'rejected' })}>cancel</span><span className="material-icons cursor-pointer color-green300" onClick={() => approve({ id: data?.id, params: 'pay', accepted: 'accepted' })}>check_circle</span></span></span>
                ))}
                </>}
              </div> : 'No Notifications !'}
            </span>
          </li>
          <li className="toa d-flex align-items-center cursor-pointer" onClick={handleSubmit}>
            <i className="material-icons" style={{ marginRight: '5px' }}>exit_to_app</i>
            {visible ? <h4>Logout</h4> : null}
          </li>
        </ul>
      </div>
    </div>
    </>
  )
}

Navbar.propTypes = {
  setRed: PropTypes.func.isRequired
};

export default Navbar;
