import axios from 'axios';
import React, { useEffect, useRef, useState } from 'react'

const ModalProf = ({ role, setRole }) => {

  const modal = useRef();

  const [load, setLoad] = useState(false);
  const [rr, setRetiu] = useState(<></>);

  const getToken = () => {
    const tokenString = localStorage.getItem('token');
    const userToken = JSON.parse(tokenString);
    return userToken?.token
  }

  const handleSubmit = (e) => {
    e.preventDefault();
    setLoad(true)

    const datas = new FormData(e.target);

    datas.append('user_id', role?.id)

    axios.post(
      `https://coperationv2.herokuapp.com/api/data`,
      datas,
      {
        headers: {
          ContentType: 'multipart/form-data',
          Authorization: `Bearer ${getToken()}`
        }
      }
    )
    .then(() => {
      setLoad(false)
      setRetiu(<></>)
    })
    .catch(error => {
      console.log(error)
      setLoad(false)
    })
  }

  useEffect(() => {
    axios.get(
      'https://coperationv2.herokuapp.com/api/user/detail',
      {
        headers: {
          'Content-Type': 'application/json',
          'Authorization': 'Bearer ' + getToken()
        }
      }
    ).then(data => {
      setRole(data.data.success);
    });
  }, [rr])

  useEffect(() => {
    setRetiu(
    <div id="modalProfile" className="modal justify-content-center align-items-start" style={{ padding: '100px 0', display: 'flex' }} ref={modal} >
      
      <div className="modal-content col-5 col-md-7 col-sm-10 wrap-b position-relative d-flex flex-direction-column p-5 overflow-unset">
        <div className="body-text align-self-center position-absolute" style={{ top: '-21px' }}>
          <h3>Data User</h3>
        </div>
        <form action="#" onSubmit={handleSubmit} style={{height: '100%'}}>
          <div className="modal-body gap-2 d-flex align-items-center justify-content-center flex-direction-column p-3">
            <div className="d-flex flex-direction-column gap-1 w-100">
              <label htmlFor="name">Name</label>
              <input className="w-100 input-modal background-gray600" type="text" name="name" />
            </div>
            <div className="d-flex flex-direction-column gap-1 w-100">
              <label htmlFor="no_telp">No Telp</label>
              <input className="w-100 input-modal background-gray600" type="number" name="no_telp" />
            </div>
            <div className="d-flex flex-direction-column gap-1 w-100">
              <label htmlFor="tanggal_lahir">Tanggal Lahir</label>
              <input className="w-100 input-modal background-gray600" type="date" name="tanggal_lahir" />
            </div>
            <div className="d-flex flex-direction-column gap-1 w-100">
              <label htmlFor="alamat">Alamat</label>
              <textarea name="alamat" rows="10" cols="30" className="w-100 input-modal background-gray600"></textarea>
              {/* <input className="w-100 input-modal background-gray600" type="text" name="alamat" /> */}
            </div>
          </div>
          <div className="d-flex justify-content-center">
            <button className="submitF cursor-pointer background-blues300 align-self-center">
              {load ? <h4>Wait...</h4> : <h4>Create</h4>}
            </button>
          </div>
        </form>
      </div>

    </div>
    )
  }, []);

  return rr;
}

export default ModalProf
