import axios from 'axios';
import React, { useEffect, useRef, useState } from 'react'
import Loader from 'react-loader-spinner';

const getToken = () => {
  const tokenString = localStorage.getItem('token');
  const userToken = JSON.parse(tokenString);
  return userToken?.token
}

const ModalPP = ({ setModal, use, href, data, keyword }) => {

  const modalR = useRef();
  const buttonR = useRef();
  const imageF = useRef();

  const [load, setLoad] = useState(false);

  const modalClose = (e) => {
    if (e.target.children[0] === modalR.current.children[0]) {
      setModal(false)
    }
  }

  useEffect(() => {
    if (use) {
      modalR.current.style.display = 'flex'
    } else {
      modalR.current.style.display = null
    }
  }, [modalR, use]);

  const submitHandle = async (e) => {
    e.preventDefault();
    setLoad(true);
    const token = getToken();
    const datas = new FormData(e.target);
    const asd = await axios.put(
      `https://coperationv2.herokuapp.com/api/${href}/${data?.id}`,
      {
        name: datas.get('name'),
        name_barang: datas.get('name_barang'),
        stock: datas.get('stock'),
        harga: datas.get('harga'),
        jenis: datas.get('jenis')
      },
      {
        headers: {
          'Authorization': 'Bearer ' + token
        }
      }
    ).then(data => {
      return {
        'success': data
      }
    }).catch(data => {
      return {
        'error': data
      }
    })
    console.log(asd)
    if (asd?.error === undefined && asd !== undefined) {
      keyword(asd => {return {...asd}})
      setLoad(false)
      buttonR.current.click();
    } else {
      setLoad(false);
    }
  }

  const submitImage = (e) => {
    e.preventDefault();
    let u = e.target.querySelector('input[name=image]');
    axios.post(
      `https://coperationv2.herokuapp.com/api/${href}/update-image/${data?.id}`,
      {
        image: u.value
      },
      {
        headers: {
          ContentType: 'multipart/form-data',
          Authorization: `Bearer ${getToken()}`
        }
      }
    ).then((data) => {imageF.current.querySelector('img').src = `https://coperationv2.herokuapp.com/img/${data.data['image Updated'].image}`;keyword(asd => {return {...asd}})}).catch(() => {});
  }

  useEffect(() => {
    if (load) {
      modalR.current.getElementsByClassName('card-modal')[0].classList.add('animate3')
    } else {
      modalR.current.getElementsByClassName('card-modal')[0].classList.remove('animate3')
    }
  }, [load]);

  return (
    <div className="modal justify-content-center align-items-start" style={{ padding: '100px 0' }} ref={modalR} onClick={modalClose}>

      <div className="modal-content col-9 col-md-11 wrap-b position-relative d-flex flex-direction-column p-5 overflow-hidden">
        <div className="card-modal" style={{ zIndex: 1, left: '25%', right: '25%' }}>
          <Loader
            type="Puff"
            color="rgba(0,90,133,1)"
            height={150}
            width={150}
            className="justify-content-center align-items-center d-flex"
            style={{
              position: 'absolute',
              top: '0',
              left: '0',
              bottom: '0',
              right: '0',
            }}
          />
        </div>
        <form ref={imageF} action="#" encType="multipart/form-data" onSubmit={submitImage}>
          <label htmlFor="imageId">
            <div className="modalImage-product">
              <input type="file" id="imageId" name="image" accept=".jpg, .jpeg, .png" hidden onChange={() => imageF.current.querySelector('input#ionIma').click()} />
              {data?.image !== undefined && <img src={`https://coperationv2.herokuapp.com/img/${data?.image}`} alt="" height="100%" />}
              <input type="submit" hidden value="submit" id="ionIma" />
            </div>
          </label>
        </form>
        <div className="modalText-product">
          <form className="gap-2 p-3" action="#" encType="multipart/form-data" onSubmit={submitHandle}>
            <div className="d-flex flex-direction-column gap-1 w-100">
              <label htmlFor={(data?.data_id === undefined) ? 'name' : 'name_barang' }>Name Product</label>
              <input className="w-100 input-modal background-gray600" type="text" name={(data?.data_id === undefined) ? 'name' : 'name_barang' } defaultValue={(data?.data_id === undefined) ? data?.name : data?.name_barang } />
            </div>
            <div className="d-flex flex-direction-column gap-1 w-100">
              <label htmlFor="jenis">Type</label>
              <input className="w-100 input-modal background-gray600" type="text" name="jenis" defaultValue={data?.jenis} />
            </div>
            <div className="d-flex flex-direction-column gap-1 w-100">
              <label htmlFor="stock">Stock</label>
              <input className="w-100 input-modal background-gray600" type="number" name="stock" defaultValue={data?.stock} />
            </div>
            <div className="d-flex flex-direction-column gap-1 w-100">
              <label htmlFor="harga">Harga</label>
              <input className="w-100 input-modal background-gray600" type="number" name="harga" defaultValue={data?.harga} />
            </div>
            <div className="d-grid gap-2" style={{ gridTemplateColumns: '1fr 1fr' }}>
              <button ref={buttonR} className="submitF cursor-pointer background-red align-self-center" type="reset" onClick={() => setModal(false)}>
                <h4>Cancel</h4>
              </button>
              <button className="submitF cursor-pointer background-blues300 align-self-center">
                <h4>Edit</h4>
              </button>
            </div>
          </form>
        </div>
      </div>

    </div>
  )
}

export default ModalPP
