import axios from 'axios';
import React, { useEffect, useRef, useState } from 'react'
import UseAnimations from 'react-useanimations';
import checkbox from 'react-useanimations/lib/checkBox'
import trash2 from 'react-useanimations/lib/trash2'
import edit from 'react-useanimations/lib/edit'
import LoaderData from './loaderdata';
import GetUserId from '../getAPI/getUserId';

const Datass = ({ keyword, setKeyword, addProduct, setdel, view, setModal, setDatas, href, dtt }) => {

  const [itemDet, setItem] = useState({
    data: null
  });
  const [loading, setLoad] = useState(true);
  const [page, setPage] = useState(1);
  const [popi, setPop] = useState();
  const [test, setTest] = useState([]);

  const cont = useRef();

  const getTY = () => {
    var t = [];
    for (let index = 0; index < itemDet?.data?.length; index++) {
      t[index] = false
    }
    setTest(t)
  }

  useEffect(() => {
    getTY();
  }, [itemDet]);

  const getToken = () => {
    const tokenString = localStorage.getItem('token');
    const userToken = JSON.parse(tokenString);
    return userToken?.token
  }

  useEffect(() => {
    const token = getToken();
    const getP = async () => {
      const dats = await axios.get(
        `https://coperationv2.herokuapp.com/api/${href}?keyword=${keyword?.keyword}&page=${page}`, 
        {
          headers: {
            'Content-Type': 'application/json',
            'Authorization': 'Bearer ' + token
          }
        }
      ).then(data => data.data);

      if (dats?.data?.[0]?.user_id !== undefined && dats?.data?.[0]?.name === undefined) {
        for (let index = 0; index < dats?.data?.length; index++) {
          const user = await GetUserId({
            id: dats?.data?.[index]?.user_id,
            token: token,
          }).then(data => data.data.user[0]);
          dats.data[index].name = user?.name;
          dats.data[index].image = user?.image;
        }
      }

      setItem(dats);
      setLoad(false)
    }
    getP();
  }, [keyword, page])

  const nextHandle = (e) => {
    setPage(parseInt(e.split('=')[1]))
  }

  const getAll = () => {
    return cont.current.querySelectorAll('.tr-hover');
  }

  const clickHandle = e => {
    e.preventDefault();
    // (test)
    var r = parseInt(e.target.id);
    test[r] = !test[r];
    setTest(test);
    e.target.checked = test[r];
    if (e.target.checked) {
      e.target.parentElement.parentElement.parentElement.classList.add('tr-hover')
    } else {
      e.target.parentElement.parentElement.parentElement.classList.remove('tr-hover')
    }
    // (test)
    setPop(getAll());
    // (popi)
  }

  useEffect(() => {
    setdel(popi)
  }, [popi, setdel]);

  const trashHandle = async e => {
    // setLoad(true);
    const token = getToken();
    let mess = {}

    axios.delete(
      `https://coperationv2.herokuapp.com/api/${href}/${parseInt(e)}`,
      {
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${token}`
        }
      }
    ).then(data => {
      mess.success = data
    }).catch(data => {
      mess.error = data
    })

    if (mess?.error === undefined) {
      setKeyword(asd => {return {...asd}})
    }
  }

  if (!loading) {

    if (view !== undefined && view === 'grid') {
      return (
        <div className="d-grid grid-product p-4 gap-3" ref={cont}>
          {itemDet?.data?.map((dat, ind) => (
            <div className="product-grid" onClick={() => {view !== undefined && setDatas(dat);setModal(true)}} key={ind}>
              <div className="image-product">
                <img src={`https://coperationv2.herokuapp.com/img/${dat?.image}`} alt="" height="100%" />
              </div>
              <div className="text-product p-2">
                <h3>{dat?.name}</h3>
                <p>Rp. {dat?.harga}</p>
                <div className="stock-product text-end">
                  <span>Stock: {dat?.stock}</span>
                </div>
              </div>
            </div>
          ))}
        </div>
      )
    }

    return (
      <>
      <table ref={cont}>
        <thead>
          <tr>
            {dtt.map((data, index) => (
              <th key={index}>{data}</th>
            ))}
            <th>Action</th>
          </tr>
        </thead>
        <tbody>
          {(itemDet?.data.length === 0)
          ? <tr>
            <td colSpan={dtt.length + 1}>
              <h3 className="color-red">No Data Found !</h3>
            </td>
          </tr>
          : itemDet.data.map((data, index) => {
            return <tr key={index} id={data?.id}>
              <td>
                <div className="position-absolute check">
                  <input type="checkbox" className='d-none' onClick={clickHandle} id={index+'check'} />
                  <label htmlFor={index+'check'}>
                    <UseAnimations animation={checkbox} reverse={test[index]} speed={3} />
                  </label>
                </div>
                {data[dtt[0].toLowerCase()]}
              </td>
              {dtt.map((dats, inda) => {
                if (inda < 1) {
                  return null;
                }
                return <td key={inda}>
                  {dats === 'Image'
                   ? <div className="img-profile position-relative" style={{ width: '50%', height: 0, paddingTop: '50%' }}>
                       <img src={`https://coperationv2.herokuapp.com/img_profile/${data[dats.replace(' ', '_').toLowerCase()]}`} alt="" className="position-absolute" style={{ top: 0 }} />
                     </div> 
                   : data[dats.replace(' ', '_').toLowerCase()]}
              </td>
              })}
              <td>
                <div className="d-flex gap-2 justify-content-center">
                  {view !== undefined && <UseAnimations animation={edit} pathCss="stroke: white" onClick={() => {setDatas(data);setModal(true)}} render={(eventP, animationP) => (
                    <button type="button" className="p-2 borderRadius-2 background-blues300" {...eventP}>
                      <div {...animationP}></div>
                    </button>
                  )} />}
                  <UseAnimations animation={trash2} pathCss="stroke: white" onClick={() => trashHandle(data?.id)} render={(eventP, animationP) => (
                    <button type="button" className="p-2 borderRadius-2 background-red300" {...eventP}>
                      <div {...animationP}></div>
                    </button>
                  )} />
                </div>
              </td>
            </tr>
          })}
        </tbody>
        <tfoot>
          <tr>
            <td colSpan={dtt.length + 1}>
              <div className="d-flex justify-content-space-between align-items-center">
                <div className="back">
                  <ul className="background-blues300 pagination d-flex flex-align-column align-items-center" style={{ borderRadius: '10px', overflow: 'hidden', }}>
                    {itemDet.links.map((data, index) => (
                      <li className={((data.active) ? 'active' : '') + ' pagination-item color-white' + ((data.url !== null) ? '' : ' button-mute' )} key={index} onClick={() => nextHandle(data.url)}>
                        {(data === itemDet.links[0])
                        ? <>&laquo;</>
                        : (data === itemDet.links[itemDet.links.length-1])
                          ? <>&raquo;</>
                          : data.label
                        }
                      </li>
                    ))}
                  </ul>
                </div>
                <div className="front">
                  <button className="color-white cursor-pointer" style={{ borderRadius: '10px', background: 'rgba(0,90,133,1)', padding: '10px 15px' }} onClick={() => addProduct(true)}>
                    + ADD {href.toUpperCase()}
                  </button>
                </div>
              </div>
            </td>
          </tr>
        </tfoot>
      </table>
      </>
    )
  } else {
    return (
      <table ref={cont}>
        <thead>
          <tr>
            {dtt.map((data, index) => (
              <th key={index}>{data}</th>
            ))}
            <th>Action</th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td colSpan={dtt.length + 1}>
              <LoaderData />
            </td>
          </tr>
        </tbody>
      </table>
    )
  }
}

export default Datass
